@import url("reset.css");
.wrapper {
  display: flex;
  flex-direction: column;
  min-width: 100%;
  overflow: hidden;
}
._container {
  max-width: 1046px;
  margin: 0 auto;
  padding: 0 15px;
  box-sizing: content-box;
}
.header {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 50;
}
.header__menu {
  margin-left: 132px;
  z-index: 100;
}
@media all and (max-width: 992px) {
  .header__menu {
    margin-left: 80px;
  }
}
@media all and (max-width: 767px) {
  .header__menu {
    margin-left: 40px;
  }
}
@media all and (max-width: 479px) {
  .header__menu {
    margin-left: 20px;
  }
}
.header__logo {
  font-size: 24px;
  font-weight: 700;
  color: inherit;
}
.header__container {
  display: flex;
  align-items: center;
  min-height: 104px;
}
.menu {
  padding: 5px 5px;
  z-index: 100;
}
.menu__list {
  display: flex;
  flex-wrap: wrap;
}
.menu__item:not(:last-child) {
  margin-right: 21px;
}
.menu__link {
  color: #737373;
  font-weight: 600;
  line-height: calc(24 / 14 * 100%);
  z-index: 101;
}
.menu__link:hover {
  text-decoration: underline;
}
@media all and (max-width: 479px) {
  .menu__link {
    font-size: 18px;
  }
}
._ibg {
  position: relative;
}
._ibg > img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}
.page {
  flex: 1 1 auto;
}
.main-block {
  position: relative;
}
.main-block__body {
  padding: 184px 0 191px 0;
  position: relative;
  z-index: 2;
  max-width: 400px;
}
.main-block__body > *:not(:last-child) {
  margin-bottom: 35px;
}
@media (max-width: 414px) {
  .main-block .main-block__title {
    font-size: 56px;
  }
}
@media (max-width: 400px) {
  .main-block .main-block__title {
    font-size: 50px;
  }
}
@media (max-width: 360px) {
  .main-block .main-block__title {
    font-size: 35px;
  }
}
.main-block__title {
  font-size: 58px;
  line-height: 138%;
  font-weight: 800;
  letter-spacing: 0.2;
}
.main-block__text {
  color: #737373;
  font-size: 20px;
  line-height: 150%;
}
.main-block__buttons {
  display: inline-flex;
}
@media (max-width: 550px) {
  .main-block__buttons {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .main-block__button:not(:last-child) {
    margin-bottom: 10px;
  }
}
.main-block__button:not(:last-child) {
  margin-right: 10px;
}
.main-block__button {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 48px;
  border-radius: 37px;
  letter-spacing: 0.2;
  font-weight: 700;
  text-align: center;
  line-height: 150%;
  padding: 5px 36px;
}
.main-block__button--border {
  border: 1px solid #0D5C63;
  color: #0D5C63;
}
.main-block__button--orange {
  background: #FF7B47;
  color: #fff;
}
.main-block__button--border:hover {
  transition: all 0.3s;
  background-color: #0D5C63;
  color: #fff;
}
.main-block__button--orange:hover {
  transition: all 0.3s;
  background-color: inherit;
  color: #FF7B47;
  border: 1px solid #FF7B47;
}
.main-block__image {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
@media all and (max-width: 992px) {
  .main-block__image {
    opacity: 0.5;
  }
}
@media all and (max-width: 1300px) {
  .main-block__image img {
    object-position: right;
  }
}
.page__services {
  position: relative;
  z-index: 2;
  margin-top: -111px;
}
.services__body {
  display: flex;
  flex-wrap: wrap;
  margin: 0px -15px;
}
@media (max-width: 1000px) {
  .services .services__column:not(:last-child) {
    margin-bottom: 20px;
  }
}
@media (max-width: 1000px) {
  .services .services__column {
    flex: 0 1 100%;
  }
}
.services__column {
  padding: 0px 15px;
  flex: 0 1 33.333%;
}
.services__item {
  height: 100%;
  box-shadow: 0px 13px 19px 0px rgba(0, 0, 0, 0.07);
  background: #fff;
  padding: 35px;
}
.item-service > *:not(:last-child) {
  margin-bottom: 10px;
}
.item-service--green {
  color: #fff;
  background: #0D5C63;
}
.item-service--green .item-service__text {
  color: #fff;
}
.item-service__title {
  font-weight: 700;
  font-size: 24px;
  line-height: 133%;
  letter-spacing: 0.1;
}
.item-service__text {
  color: #737373;
  line-height: 143%;
}
.header-block {
  text-align: center;
  letter-spacing: 0.2;
}
.header-block__title {
  font-weight: 700;
  line-height: 142%;
  font-size: 40px;
}
.header-block__title:not(:last-child) {
  margin-bottom: 10px;
}
.header-block__sub-title {
  line-height: 142%;
  color: #737373;
}
.advantages {
  padding: 80px 0;
}
@media (max-width: 1000px) {
  .advantages .advantages__column {
    flex: 0 1 50%;
  }
  .advantages .advantages__column:not(:last-child) {
    margin-bottom: 20px;
  }
}
@media (max-width: 560px) {
  .advantages .advantages__column {
    flex: 0 1 100%;
  }
}
.advantages__icon {
