  font-size: 16px;
}
.practice {
  padding: 125px 0 80px 0;
}
.practice__header {
  margin-bottom: 45px;
}
.practice__body {
  display: flex;
  flex-wrap: wrap;
  margin: 0px -15px;
}
.practice__column {
  padding: 0px 15px;
  flex: 0 1 25%;
}
@media (max-width: 1000px) {
  .practice .practice__column {
    flex: 0 1 50%;
  }
}
@media (max-width: 360px) {
  .practice .practice__column {
    flex: 0 1 100%;
  }
}
@media (max-width: 1000px) {
  .practice {
    padding: 40px 0 80px 0;
  }
}
.item-practice {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: #fff;
}
.item-practice__content {
  padding: 30px;
  flex: 1 1 auto;
}
.item-practice__title {
  color: #252B42;
  font-size: 16px;
  font-weight: 700;
  line-height: 150%;
  letter-spacing: 0.1;
  margin-bottom: 15px;
}
.item-practice__text {
  color: #737373;
  font-weight: 500;
  font-size: 14px;
  line-height: 143%;
  letter-spacing: 0.2;
}
.item-practice__link:hover {
  text-decoration: underline;
}
.item-practice__image {
  display: block;
  padding-bottom: 60%;
}
.whoweare {
  background-color: #fff;
  padding: 100px 0 100px 0;
}
.whoweare__header {
  margin-bottom: 100px;
}
.whoweare__body {
  display: flex;
  align-items: flex-start;
}
.whoweare__video {
  flex: 0 0 57%;
  position: relative;
  padding: 0 0 42.25% 0;
}
.whoweare__video iframe {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}
.whoweare__content {
  flex: 1 1 auto;
  padding-left: 120px;
}
@media (max-width: 1080px) {
  .whoweare .whoweare__content {
    padding-left: 50px;
  }
}
.whoweare__title {
  font-weight: 700;
  font-size: 40px;
  line-height: 142%;
  letter-spacing: 0.2;
}
.whoweare__text {
  color: #737373;
  line-height: 142%;
}
.whoweare__top {
  padding: 20px 0;
}
.whoweare__top > *:not(:last-child) {
  margin-bottom: 10px;
}
.whoweare__item {
  margin-top: 30px;
}
@media (max-width: 950px) {
  .whoweare {
    padding: 50px 0 50px 0;
  }
  .whoweare__video {
    padding: 0 0 56.25% 0;
  }
  .whoweare__content {
    align-self: flex-start;
  }
  .whoweare__body {
    flex-direction: column;
    align-items: stretch;
  }
  .whoweare__body > *:not(:last-child) {
    margin-bottom: 50px;
  }
}
.item-whoweare {
  display: flex;
}
.item-whoweare__icon {
  flex: 0 0 32px;
}
.item-whoweare__body {
  flex: 1 1 auto;
  padding-left: 20px;
}
.item-whoweare__title {
  font-weight: 700;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.1;
}
.item-whoweare__title:not(:last-child) {
  margin-bottom: 5px;
}
.item-whoweare__text {
  font-weight: 600;
  font-size: 14px;
  line-height: 171%;
  letter-spacing: 0.2;
  white-space: nowrap;
  width: 222px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.clients {
  padding: 50px 0;
}
.clients__items {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.clients__item {
  padding: 8px 15px;
  min-width: 140px;
  text-align: center;
}
.clients__item:not(:last-child) {
  margin-right: 30px;
}
.clients__item img {
  max-width: 100%;
}
@media (max-width: 810px) {
  .subscribe__body {
    flex-direction: column;
    margin: 0 -15px;
    padding: 20px 15px;
  }
  .subscribe__title {
    text-align: center;
  }
  .subscribe__form {
    width: 100%;
    margin-top: 15px;
  }
}
.subscribe {
  position: relative;
  padding: 20px 30px;
}
.subscribe::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 77px;
  left: 0;
  bottom: 0;
  background: #252B42;
  z-index: -1;
}
.subscribe__title {
  font-weight: 700;
  font-size: 24px;
  line-height: 133%;
  letter-spacing: 0.1px;
  color: #fff;
  max-width: 266px;
}
.subscribe__body {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px 50px;
  background: #17213C;
  border-radius: 2px;
  border-collapse: collapse;
}
.subscribe__form {
  flex: 0 1 50%;
  display: flex;
  align-items: center;
}
.subscribe__form-input {
  flex: 1 1 auto;
  min-height: 58px;
  border-right: none;
  border-radius: 5px 0 0 5px;
  padding: 0 20px;
  color: #737373;
}
.subscribe__form-input:focus {
  color: #000;
}
.subscribe__button {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  cursor: pointer;
  background: #FF7B47;
  border: 1px solid #E6E6E6;
  border-radius: 0px 5px 5px 0px;
  min-height: 58px;
  padding: 0 22px;
  color: #fff;
}
.subscribe__button:hover {
  transition: all 0.3s;
  background-color: #a54722;
}
@media (max-width: 500px) {
  .subscribe__form {
    display: flex;
    flex-direction: column;
  }
  .subscribe__form-input {
    flex: 1 1 100%;
    width: 100%;
    border-radius: 5px 5px 0 0;
  }
  .subscribe__button {
    width: 100%;
    border-radius: 0 0 5px 5px;
  }
}
